.current-weather-container{
    grid-row: 2/3;
    display: grid;
    grid-template-columns: 400px;
    grid-template-rows: auto 200px;
    padding: 10px;
    min-width: 400px;
    -webkit-justify-content: center;
            justify-content: center;
}

.weather-card{
    grid-row: 1/2;
    grid-column: 1/2;
    /*background: rgba(134, 134, 134, 0.7);*/
    background: rgba(97, 36, 194, 0.9);
    color: white;
    width: 100%;
    border-radius: 15px;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 20% 25% 30% 20%;
    font-size: 2em;
}

.weather-card h1 {
    font-size: 15vw;
}

.weather-card-city{
    grid-row: 1/2;
    grid-column: 1/2;
    width: 100%;
    margin: auto;
}

.weather-card-icon{
    grid-row: 2/3;
    grid-column: 1/2;
    margin: auto;
}

.weather-card-icon img{
    height: 200%;
}

.weather-card-temperature{
    grid-row: 3/4;
    grid-column: 1/2;
}

.weather-card-description{
    grid-row: 4/5;
    grid-column: 1/2;
}

.weather-details-card{
    grid-row: 2/3;
    grid-column: 1/2;
    /*background: rgba(134, 134, 134, 0.7);*/
    background: rgba(107, 59, 184, 0.9);
    color: white;
    border-radius: 15px;
    width:100%; height:100%;
    margin-top: 10px;
    padding: 5%;
    font-size: 1em;
    font-size: 3vw;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
}
.weather-details-card h3{
    font-size: 1.5em;
}


@media only screen and (min-width: 800px) {

